import "./styles.css";
// import Todo from "./Components/Todo";
// import Counter from "./Components/Counter";
import React, { useEffect, useState } from "react";
import "./App.css";
import Recipe from "./Components/Recipe";

export default function App() {
  const [recipes, setRecipes] = useState([]);
  const [search, setSearch] = useState("");
  const [query, setQuery] = useState("Chicken");
  useEffect(() => {
    getRecipes();
  }, [query]);
  const getRecipes = async () => {
    const response = await fetch(
      `https://www.themealdb.com/api/json/v1/1/search.php?s=curry`
    );
    const data = await response.json();
    setRecipes(data.meals);
  };
  const updateSearch = (e) => {
    setSearch(e.target.value);
  };
  const getSearch = (e) => {
    e.preventDefault();
    setQuery(search);
    setSearch(search);
  };
  return (
    <>
      <div className="App">
        <form className="search-form" onSubmit={getSearch}>
          <input
            className="search-bar"
            type="text"
            value={search}
            onChange={updateSearch}
          />
          <button className="search-button" type="submit">
            Search
          </button>
        </form>
        <div className="recipes">
          {recipes
            .filter((val) => {
              if (setSearch === "") {
                return val;
              } else if (
                val.strMeal.toLowerCase().includes(search.toLowerCase())
              ) {
                return val;
              }
            })
            .map((recipe) => (
              <Recipe
                search={search}
                key={recipe.idMeal}
                title={recipe.strMeal}
                image={recipe.strMealThumb}
                category={recipe.strCategory}
                area={recipe.strArea}
                instructions={recipe.strInstructions}
                ingredient1={recipe.strIngredient1}
                ingredient2={recipe.strIngredient2}
                ingredient3={recipe.strIngredient3}
                ingredient4={recipe.strIngredient4}
                ingredient5={recipe.strIngredient5}
                ingredient6={recipe.strIngredient6}
                ingredient7={recipe.strIngredient7}
                ingredient8={recipe.strIngredient8}
                ingredient9={recipe.strIngredient9}
              />
            ))}
        </div>
      </div>
    </>
  );
}
