import React, { useEffect } from "react";
import style from "./recipe.module.css";

const Recipe = ({
  title,
  image,
  search,
  category,
  area,
  instructions,
  ingredient1,
  ingredient2,
  ingredient3,
  ingredient4,
  ingredient5,
  ingredient6,
  ingredient7,
  ingredient8,
  ingredient9,
  setText,
  text
}) => {
  const [Opacity, setOpacity] = React.useState("");

  const styles = {
    opacity: Opacity
  };
  function onEnter(e) {
    setOpacity("0.5");
  }
  function onLeave(e) {
    setOpacity("");
  }
  // useEffect(() => {
  //   });
  function Clicked() {
    setText((prev) => title);
    console.log(text);
    console.log(title);
  }

  return (
    <div>
      {search === "" ? <> </> : <h4>Search results for '{search}'</h4>}
      <div className={style.recipe}>
        <h1>{title}</h1>
        <div onClick={Clicked}>
          <div
            id="cspace"
            title={title}
            style={styles}
            onMouseEnter={onEnter}
            onMouseLeave={onLeave}
          >
            <img className={style.image} src={image} alt="" />
          </div>
        </div>
      </div>
      {search === "" ? (
        <> </>
      ) : (
        <div>
          <h3>{title}</h3>
          <img className={style.image} src={image} alt="" />
          <div className={style.recipe}>
            <h4>{category}</h4>
            <h6>{area}</h6>
          </div>
          <p>{instructions}</p>
          <h2>Ingredients</h2>
          <b className={style.block}>{ingredient1}</b>
          <b className={style.block}>{ingredient2}</b>
          <b className={style.block}>{ingredient3}</b>
          <b className={style.block}>{ingredient4}</b>
          <b className={style.block}>{ingredient5}</b>
          <b className={style.block}>{ingredient6}</b>
          <b className={style.block}>{ingredient7}</b>
          <b className={style.block}>{ingredient8}</b>
          <b className={style.block}>{ingredient9}</b>
        </div>
      )}
    </div>
  );
};
export default Recipe;
